:root{
  --primary:#d81e5b;
  --secondary:#8c38ff;
  --dark:#131a26;
  --light:#eee;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'fira Sans', sans-serif;
}
body{
  background-color: var(--light);
}
.App{
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  padding: 16px;
}
.calculator{
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0,0,0,0.2);
}
.display{
  padding: 20px;
  text-align: right;
  background-color: var(--dark);
  color: var(--light);
  font-size: 24px;
  font-weight: 300;
  position: relative;
 

}
.display h6{
  font-size: 10px;
  color: #888;
  padding: 10px;

}
button{
  appearance: none;
  border: none;
  outline: none;
  color: #fff;
  font-size: 20px;
  padding: 16px;
  cursor: pointer;
  transition: 0.4s;
}
button:hover {
  opacity: 0.5;
}

.digits button:hover {
  opacity: 0.9;
  color: var(--primary);
}
.operators{
  display: flex;
  
}
.operators button{
  flex: 1 1 0%;
  background-color: var(--primary);
  font-weight: 700;
  border: var(--secondary) solid 1px;
}
.digits{
  display: flex;
  flex-wrap: wrap;
}
.digits button{
  flex: 1 1 33.333%;
  max-width: 33.333%;
  background-color: var(--dark);
  border: var(--secondary) solid 1px;
}